<template>
  <div class="Mbox">
    <!-- 外层容器 -->
    <el-container :class="['box']">
      <!-- 头 -->
      
      <el-header :class="['top']">
        <!-- tabs看见 -->
        <div class="top_logo">{{companyname!='无' ? companyname:'达摩智联'}}</div>
        <!-- 侧边栏的展开和收起 -->
        <!-- <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
          <el-radio-button :label="false">展开</el-radio-button>
          <el-radio-button :label="true">收起</el-radio-button>
        </el-radio-group> -->
        <svg class="icon iconDefault" @click="isCollapse=!isCollapse">
              <use xlink:href="#icon-liebiao"></use>
        </svg>
        <!-- 登陆的头像 -->
        <div class="hdB_rigth">
            <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link">
                {{userInfo.username}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">个人信息</el-dropdown-item>
                <el-dropdown-item command="e" divided>退出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
        </div>
        <!-- 侧边栏的展开和收起结束 -->
      </el-header>
      <!-- 中间，包含左侧边栏，和主体部分 -->
      <el-container class="mainBox">
        <!-- 侧边栏内容显示 -->
        <!-- <el-aside class="asideBox">
          
        </el-aside> -->
        <!-- 主体内容 -->
        <el-main class="main">
          <el-menu text-color="#fff"
           active-text-color="#ffd04b"
           @select="handleSelect"
           background-color="#545c64"
           :default-active="defaultActive"
           class="el-menu-vertical-demo" 
           :collapse="isCollapse">
            <el-menu-item index="5">
              <span slot="title">项目管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="1">
              <span slot="title">添加设备</span>
            </el-menu-item> -->
            <el-menu-item index="2">
              <span slot="title">我的设备</span>
            </el-menu-item>
            <el-menu-item index="3" :disabled="false">
              <span slot="title">员工管理</span>
            </el-menu-item>
            <!-- <el-menu-item index="4">
              <span slot="title">权限管理</span>
            </el-menu-item> -->
            <!-- <el-menu-item index="6">
              <span slot="title">系统设置</span>
            </el-menu-item> -->
            <el-menu-item index="7">
              <span slot="title">去首页</span>
            </el-menu-item>
          </el-menu>
          <!-- 这是子项目展开 -->
          <div class="main_left">
            <router-view/>
          </div>
        </el-main>
      </el-container>
      <!-- 底部信息 -->
      <!-- <el-footer class="footer">这是底部</el-footer> -->
    </el-container>
  </div>
</template>
<script>
import { mapState,mapActions } from "vuex";
export default {
    data() {
      return {
        defaultActive:sessionStorage.getItem('defaultActiveh') || "5",
        isCollapse: false
      };
    },
    methods: {
      ...mapActions(['DelAuserInfo']),
      handleSelect(key, keyPath) {//这是侧边栏的点击效果
          sessionStorage.setItem("defaultActiveh",key);
          this.defaultActive=sessionStorage.getItem('defaultActiveh')
          console.log(key, keyPath,'sel-----------');
          switch (key) {
            case "1"://添加设备
              this.$router.push('/index/addfacility')
              break;
            case "2"://我的设备
              this.$router.push('/index/managefacility')
              break;
            case "3"://部门信息
              this.$router.push('/index/userManagement')
              break;
            case "4"://权限管理
              this.$router.push('/index/powerfacility')
              break;
            case "5"://项目管理
              this.$router.push('/index/MFprojectManagement')
              break;
            case "6"://系统设置？？？
              this.$router.push('/index/powerfacility')
              break;
            case "7"://系统设置？？？
              this.$router.push('/newsy')
              break;
            default://默认跳转到我的设备页面
              this.$router.push('/index/managefacility')
              break;
          }
      },
      handleCommand(command) {//这是登陆上面的处罚点击按钮
          // command--点击的子菜单的项目command的值
          if(command=='a'){
            this.$router.push('/UserInformation/UItwo')
          }else if(command=='e'){
            this.DelAuserInfo()//退出删除vuex中的参数
            this.$router.push('/login')
          }
      },
    },
    computed:{
      ...mapState(['userInfo','companyname'])
    },
    beforeRouteEnter(to,from,next){//和全局使用一样
      console.log(sessionStorage.getItem('logi'),'------------hsxsx')
      let xx=sessionStorage.getItem('logi');
      if(xx!='成功'){
        next('/login')
      }else{
        next()
        return
      }
    },
};
</script>
<style lang='stylus' scoped>
  .Mbox
    height 100vh
  .box {
    height 100vh
  }
  .mainBox
    height 100%


  .top 
    background: #333;
    .hdB_rigth
        float right
        margin-top 20px
        .el-dropdown-link
            font-size 14px
            color #ffffff
  .top>div{
      float: left;
  }
  .top>ul{
      float: left;
  }
  .iconDefault
    font-size 30px
    color #999
    margin-top 15px
  .top_logo{
      color: #fff;
      font-weight: bold;
      font-size: 24px;
      line-height: 59px;
      margin-right: 30px;
  }
  .left {
    border: 2px solid #00f;
    box-sizing: 22px;
    border-radius: 50%;
    border: 1px solid #f2f2f2;
    padding-top: 8px;
  }
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
  }
  .asideBox
    max-width 201px
  .main
    display flex
    padding 0
    height 100%
    .main_left
      flex 1
      margin-left 17px
      overflow-x: scroll;
      overflow-y: scroll;

  .footer {
    background: rgb(84, 92, 100);
    line-height 60px
    color #fff
  }

</style>


